/* ===============================
   Friend Links 美化（Stack + PaperMod 风格）
   圆角卡片 + 动态阴影 + 亮暗自适应
=================================*/

.friend-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  gap: 1rem;
  margin-top: 1rem;
}

/* 每个友链卡片 */
.friend.block.whole {
  display: flex;
  align-items: center;
  width: calc(33.333% - 1rem);
  min-width: 280px;
  height: 90px;
  background: var(--card-bg);
  color: var(--card-text);
  border-radius: 12px;
  box-shadow: 0 3px 10px var(--shadow-color);
  transition: transform 0.25s ease, box-shadow 0.25s ease;
  cursor: pointer;
  overflow: hidden;
  border: 1px solid rgba(0, 0, 0, 0.05);
}

.friend.block.whole:hover {
  transform: translateY(-3px);
  box-shadow: 0 6px 20px var(--shadow-color);
  border-color: var(--accent-color);
}

/* 左侧头像 */
.friend.block.left {
  flex-shrink: 0;
  width: 70px;
  height: 70px;
  margin-left: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.friend.block.left img {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  border: 2px solid transparent;
  transition: transform 0.4s ease, border-color 0.3s ease;
}
.friend.block.whole:hover .friend.block.left img {
  transform: rotate(8deg) scale(1.05);
  border-color: var(--accent-color);
}

/* 右侧文字 */
.friend.block.right {
  margin-left: 1rem;
  overflow: hidden;
}

.friend.name {
  font-weight: 600;
  font-size: 1.05rem;
  color: var(--card-text);
  margin-bottom: 0.2rem;
  transition: color 0.25s ease;
}
.friend.block.whole:hover .friend.name {
  color: var(--accent-color);
}

.friend.info {
  font-size: 0.9rem;
  color: #7a7a7a;
  opacity: 0.85;
  line-height: 1.3;
}
[data-scheme="dark"] .friend.info {
  color: #9ca3af;
}

/* 自适应布局（移动端） */
@media screen and (max-width: 900px) {
  .friend.block.whole {
    width: 100%;
    height: auto;
    padding: 0.8rem;
  }
  .friend.block.left {
    margin-left: 0.5rem;
  }
  .friend.block.right {
    margin-left: 0.8rem;
  }
}

/* 轻微动态闪光效果（hover） */
.friend.block.whole::after {
  content: "";
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: linear-gradient(120deg, transparent, rgba(255, 255, 255, 0.3), transparent);
  transform: translateX(-100%) rotate(25deg);
  transition: transform 0.8s ease;
  opacity: 0;
}
.friend.block.whole:hover::after {
  transform: translateX(100%) rotate(25deg);
  opacity: 1;
}

/* 暗色模式优化 */
[data-scheme="dark"] .friend.block.whole {
  background: #1b1d22;
  border: 1px solid rgba(255, 255, 255, 0.08);
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.6);
}
[data-scheme="dark"] .friend.block.whole:hover {
  border-color: var(--accent-color);
}
